.walletContainer {
  background-color: white;
  border-radius: 10px;
  box-shadow: -4px 9px 13px rgba(5, 18, 54, 0.4);
  display: flex;
  flex-flow: column wrap;
  height: 385px;
  position: absolute;
  top: 0;
  width: 1037px;
}

.headerContainer {
  align-items: center;
  align-self: flex-start;
  display: flex;
  flex-flow: row wrap;
  height: 100px;
  justify-content: space-around;
  margin: 25px 0;
  width: 100%;
}

.logoTrybeWalletHeader {
  background-image: url('../imgs/logoTrybeWallet.png');
  background-repeat: no-repeat;
  background-size: cover;
  height: 57px;
  justify-content: center;
  mix-blend-mode: normal;
  width: 269px;
}

.expenseContainer {
  align-items: center;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  margin-top: 14px;
}

.imgTotalExpense {
  background-image: url('../imgs/Moedas.png');
  background-repeat: no-repeat;
  background-size: cover;
  height: 25px;
  margin-right: 5px;
  width: 25px;
}

.txtExpense {
  color: #003be5;
  font-weight: 700;
}

.txtExpenseTotal {
  font-weight: 400;
  margin: 0 5px;
}

.txtExpenseCurrency {
  font-weight: 400;
}

.mailContainer {
  align-items: center;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  margin-top: 14px;
}

.txtEmail {
  color: #2fc18c;
  font-weight: 600;
}

.imgUserProfile {
  background-image: url('../imgs/user.png');
  background-repeat: no-repeat;
  background-size: cover;
  height: 25px;
  margin-right: 5px;
  width: 25px;
}

.walletFormContainer {
  background: rgba(225, 229, 235, 0.49);
  display: flex;
  flex-flow: row wrap;
  height: 135px;
  margin-bottom: 30px;
  width: 100%;
}

.walletFormContainer label {
  color: #003be5;
  font-weight: 700;
}

.formExpense {
  display: flex;
  flex-flow: row wrap;
  margin: 30px 90px;
}

.walletFormContainer input {
  background-color: transparent;
  border: 1px solid #003be5;
  border-radius: 5px;
  color: #003be5;
  height: 30px;
  padding: 10px;
}

.walletFormContainer select {
  background-color: transparent;
  border: 1px solid #003be5;
  border-radius: 5px;
  color: #003be5;
  height: 30px;
  padding-left: 10px;
}

.walletFormContainer input::placeholder , .walletFormContainer select::placeholder {
  color: #003be5;
}

.walletFormContainer input:focus , .walletFormContainer select:focus {
  box-shadow: 0 0 10px #003be5;
  outline: none;
}

.inputExpense {
  margin-left: 10px;
  margin-right: 30px;
  width: 288.86px;
}

.selectCategory {
  margin-left: 10px;
  width: 155px;
}

.inputValue {
  margin-left: 10px;
  margin-right: 30px;
  margin-top: 25px;
  width: 158px;
}

.inputPayment {
  margin-left: 10px;
  margin-right: 31px;
  margin-top: 25px;
  width: 228px;
}

.inputCoin {
  margin-left: 10px;
  margin-top: 25px;
  width: 91px;
}

.btnAddExpenseContainer {
  display: flex;
  flex-flow: row wrap;
  height: 38px;
  margin-top: 20px;
  width: 100%;
}

.btnAddExpense {
  background-color: #2fc18c;
  border: 1px solid #2fc18c;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  font-weight: 700;
  height: 40px;
  margin: auto;
  width: 330px;
}

.tableContainer {
  background-color: #003be5;
  border-radius: 10px;
  bottom: 0;
  box-shadow: -4px 9px 13px rgba(3, 107, 82, 0.3);
  left: -60px;
  margin: auto;
  max-height: auto;
  min-height: 464px;
  position: absolute;
  top: 380px;
  width: 1155px;
  z-index: -1;
}

.expenseTableContainer {
  height: 170px;
  margin: auto;
  margin-top: 275px;
  overflow: auto;
  width: 1037px;
}

.expenseTable {
  border-collapse: collapse;
  font-size: 12px;
  margin: auto;
  text-align: center;
  width: 100%;
}

.expenseTableHeader {
  color: white;
}

.expenseTableHeader th:not( :first-child ) {
  border-left: 1px solid white;
  height: 23px;
  padding: 0 5px;
  width: 110px;
}

.expenseTableHeader th:nth-child( 5 ) {
  width: 140px;
}

.gap {
  border-bottom: 1px solid white;
  height: 10px;
}

.expenseTableContent {
  color: #2fc18c;
}

.greenBorderTop {
  border-bottom: 1px solid #2fc18c;
  height: 41px;
}

.btnEditExpense {
  background-color: transparent;
  background-image: url('../imgs/Editar.png');
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  height: 15px;
  margin-right: 15px;
  width: 15px;
}

.btnDeleteExpense {
  background-color: transparent;
  background-image: url('../imgs/del.png');
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  height: 15px;
  width: 15px;
}
